<template>
    <div id="container" class='right-box'></div>
</template>

<script>
    import {EventMgr, SEARCH_EVENT} from '../util/EventMgr';
    import {addMarker} from '../util/Marker';

    export default{
        data(){
            return{
                container: 'container',
                map: null
            }
        },
        mounted() {
            this.initMap();
            this.renderMkr();
        },
        methods: {
            initMap() {
                let pos = new AMap.LngLat(116.397428, 39.90923);

                let mapObj = new AMap.Map(this.container, {
                    resizeEnable: true,
                    center: pos, //
                    zoom: 14, //
                    rotation: 0 //
                }); //

                let scale = new AMap.Scale({
                        visible: true
                    }),
                    toolBar = new AMap.ToolBar({
                        visible: true
                    }),
                    overView = new AMap.OverView({
                        visible: true
                    });
                mapObj.addControl(scale);
                mapObj.addControl(toolBar);
                mapObj.addControl(overView);

                this.map = mapObj;
            },
            renderMkr() {
                EventMgr.$on(SEARCH_EVENT, (list) => {
//                    console.log('add mkr:', list);
                    addMarker(this.map, list);
                });
            }
        }
    }
</script>



